// UserContext.js
import React, { createContext, useContext, useState, useEffect } from 'react';
// import axiosReq from '../components/axiosReq';
import axios from 'axios';


const UserContext = createContext(null);

export const useUser = () => useContext(UserContext);

export const UserProvider = ({ children }) => {
  const [user, setUser] = useState(null);

  useEffect(() => {
    console.log('User in NavBar:', user);
    console.log('Access token:', localStorage.getItem('access_token')); 
    console.log('Refresh token:', localStorage.getItem('refresh_token'));
  }, [user]); // Logga anvndarstaten nr den ndras


  // useEffect(() => {
  //   // Om ingen anvndare r inloggad, stt en default 'guestuser'
  //   if (!user) {
  //     setUser({ username: 'guestuser' });
  //   }
  // }, [user, setUser]);


  useEffect(() => {
    const validateAndRefreshToken = async () => {
      const accessToken = localStorage.getItem('access_token');
      const refreshToken = localStorage.getItem('refresh_token');
  
      if (accessToken && refreshToken) {
        try {
          // Validera access token genom att gra en frfrgan till en skyddad resurs
          const response = await axios.get('/api/user/', {
            headers: {
              Authorization: `Bearer ${accessToken}`,
            },
          });
          // Om frfrgan lyckas, r access token fortfarande giltig
          // Spara bde token och anvndarinformation frn serverns svar
          setUser({ 
            token: accessToken, 
            ...response.data 
          });
        } catch (error) {
          if (error.response && error.response.status === 401) {
            // Om access token r utgnget, anvnd refresh token fr att f ett nytt access token
            try {
              const tokenResponse = await axios.post('/api/token/refresh/', { refresh: refreshToken });
              const newAccessToken = tokenResponse.data.access;
              localStorage.setItem('access_token', newAccessToken);
              // Efter att ha ftt en ny access token, hmta anvndarinformation igen
              const userResponse = await axios.get('/api/user/', {
                headers: {
                  Authorization: `Bearer ${newAccessToken}`,
                },
              });
              setUser({ 
                token: newAccessToken, 
                ...userResponse.data 
              });
            } catch (refreshError) {
              // Om frnyelse misslyckas, logga ut anvndaren
              logOut();
            }
          }
        }
      }
    };
  
    validateAndRefreshToken();
  }, []);


  const signIn = async (username, password) => {
    try {
      const response = await axios.post('/api/token/', {
        username,
        password,
      });
      // Spara JWT i localStorage eller p annat skert stt
      localStorage.setItem('access_token', response.data.access);
      localStorage.setItem('refresh_token', response.data.refresh);
      // Stt anvndarstaten med relevant information
      setUser({ username });
      // Returnera true om inloggningen var framgngsrik
      return true;
    } catch (error) {
      // Hantera fel
      console.error('Login error', error);
      // Returnera false om inloggningen misslyckades
      return false;
    }
  };

  // Funktion fr att logga ut anvndaren
  const logOut = () => {
    // Ta bort JWT frn lokal lagring
    localStorage.removeItem('access_token');
    localStorage.removeItem('refresh_token');
    setUser(null);

  };

  return (
    <UserContext.Provider value={{ user, signIn, logOut }}>
      {children}
    </UserContext.Provider>
  );
};