import React from 'react';
import { Dropdown } from 'react-bootstrap';
import ClownfiskImage from '../../assets/images/clownfisk.png';
import SvrdfiskImage from '../../assets/images/svrdfisk2.png';
import BlsfiskImage from '../../assets/images/blsfisk.png';
import BlckfiskImage from '../../assets/images/blckfisk.png';

function CustomDropdown({ fishData = {}, handleInputChange }) {
  return (
    <Dropdown onSelect={handleInputChange}>
      <Dropdown.Toggle variant="success" id="dropdown-basic">
        {fishData.fish_type || "Vlj en fishtyp"}
      </Dropdown.Toggle>

      <Dropdown.Menu>
        <Dropdown.Item eventKey="svrdfisk">
          <img src={SvrdfiskImage} alt="Svrdskfish" style={{ width: '70px', height: 'auto' }}/>
        </Dropdown.Item>
        <Dropdown.Item eventKey="clownfisk">
          <img src={ClownfiskImage} alt="Clownfisk" style={{ width: '70px', height: 'auto' }}/>
        </Dropdown.Item>
        <Dropdown.Item eventKey="blckfisk">
          <img src={BlckfiskImage} alt="Blckfisk" style={{ width: '70px', height: 'auto' }} />
        </Dropdown.Item>
        <Dropdown.Item eventKey="blsfisk">
          <img src={BlsfiskImage} alt="Blsfisk" style={{ width: '70px', height: 'auto' }}/>
        </Dropdown.Item>
      </Dropdown.Menu>
    </Dropdown>
  );
}

export default CustomDropdown;