import React from 'react';
import { useSpring, animated } from '@react-spring/web';

import bubbelImage from '../../assets/images/bubblabl.png';


const getRandomSize = () => Math.random() * (60 - 10) + 10;

const BubbleAnimated = ({ index }) => {
    // const bubbelSize = 50; // Bredden p bubblan
    const bubbelSize = getRandomSize();
    const getRandomXPosition = () => Math.random() * (window.innerWidth - bubbelSize);
  
    const [style, api] = useSpring(() => ({
      from: {
        x: getRandomXPosition(), // Slumpmssig startposition i x-led
        y: window.innerHeight + bubbelSize, // Starta utanfr skrmen nedtill
      },
      to: {
        y: -bubbelSize, // Flytta bubblan uppt tills den r utanfr skrmen
      },
      config: {
        duration: 20000, // Varaktighet fr att bubblan ska n toppen
      },
      delay: index * 600, // Frskjutning baserat p index fr att bubblorna ska starta vid olika tillfllen
      onRest: () => {
        // terstll animationen nr den r frdig
        api.start({
          from: { x: getRandomXPosition(), y: window.innerHeight + bubbelSize },
          to: { y: -bubbelSize },
          config: { duration: 15000 },
          delay: index * 600,
        });
      },
    }));
  
    return (
      <animated.div
        style={{
          ...style,
          position: 'absolute',
          width: `${bubbelSize}px`,
          height: `${bubbelSize}px`,
          backgroundImage: `url(${bubbelImage})`,
          backgroundSize: 'contain',
          backgroundRepeat: 'no-repeat',
          // Lgg till andra stilar du vill ha fr din bubbla
        }}
      />
    );
  };
  
const Bubbles = ({ count = 20 }) => {
    return (
        <>
        {Array.from({ length: count }).map((_, index) => (
            <BubbleAnimated key={index} index={index} />
        ))}
        </>
    );
    };

export default Bubbles;