// axiosReq.js
import axios from 'axios';


const axiosReq = axios.create({
  baseURL: 'http://127.0.0.1:8000/api', 
});

// Lgg till en interceptor som stter Authorization-headern
axiosReq.interceptors.request.use(config => {
  // Hmta token frn dr den lagras (t.ex. localStorage)
  const token = localStorage.getItem('access_token');
  if (token) {
    config.headers['Authorization'] = `Bearer ${token}`;
  } else {
    // Ta bort Authorization-headern om ingen token finns
    delete config.headers['Authorization'];
  }
  return config;
}, error => {
  return Promise.reject(error);
});


// Response interceptor fr att hantera tokenfrnyelse
axiosReq.interceptors.response.use(
  response => response, // Om svaret r OK, returnera det direkt
  async error => {
    // Om ett fel uppstr och det r en 401 Unauthorized
    if (error.response && error.response.status === 401) {
      const originalRequest = error.config;
      // Kontrollera om vi redan har frskt att frnya token
      if (!originalRequest._retry) {
        originalRequest._retry = true; // Markera att vi frsker frnya token
        const refreshToken = localStorage.getItem('refresh_token');
        try {
          // Frsk att frnya access token med refresh token
          const response = await axiosReq.post('/token/refresh/', {
            refresh: refreshToken,
          });
          // Om frnyelsen lyckas, spara den nya access token
          localStorage.setItem('access_token', response.data.access);
          // Uppdatera access token i den ursprungliga frfrgningen och skicka om den
          originalRequest.headers['Authorization'] = `Bearer ${response.data.access}`;
          return axiosReq(originalRequest);
        } catch (refreshError) {
          // Om tokenfrnyelsen misslyckas, hantera det enligt din app-logik
          console.error("Unable to refresh token", refreshError);
          // Du kan vlja att inte logga ut anvndaren hr och bara returnera ett fel
          return Promise.reject(refreshError);
        }
      }
    }

    // Fr alla andra svarstyper som inte r 401, eller om vi redan har frskt att frnya token, returnera felet
    return Promise.reject(error);
  }
);

export default axiosReq;